import React from 'react';
import styled from "styled-components";
import {useDispatch, useSelector} from "react-redux";
import {userUnderstands} from "../redux/attentionSlice";

const Popup = styled.div`
  //display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0,0,0,.7);
  z-index: 99999;
`
const Inner = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`
const Box = styled.div`
  max-width: 570px;
  width: 100%;
  padding: 30px;
  background-color: white;
`
const Title = styled.h2`
  font-size: 32px;
  text-align: center;
  margin-bottom: 30px;
  text-decoration: underline #01ab90;
  text-decoration-thickness: 5px;
  text-underline-offset: 8px;
`
const Text = styled.p`
  font-size: 18px;
`
const ButtonWrapper = styled.div`
  text-align: center;
`
const Button = styled.button`
  cursor: pointer;
  appearance: none;
  border: 4px solid #01ab90;
  font-size: 24px;
  padding: 10px 30px;
  margin-top: 30px;
  font-weight: 600;
  background-color: white;
  transition: .3s;
  &:hover{
    border-color: black;
    color: white;
    background-color: #01ab90;  
  }
`


const AttentionPopup = () => {
    let dispatch = useDispatch()

    let handleClick = () => {
        dispatch(
            userUnderstands(true)
        )
    }

    return (
        <Popup>
            <Inner>
                <Box>
                    <Title>
                        Attention!
                    </Title>
                    <Text>
                        This is a frontend version of this project.
                        Since this project is on github pages, it has no backend
                        and that's why you won't be able to interact with
                        everything that relies on server side.
                    </Text>
                    <ButtonWrapper>
                        <Button onClick={handleClick}>Yeah, I understand that</Button>
                    </ButtonWrapper>
                </Box>
            </Inner>
        </Popup>
    );
}

export default AttentionPopup;