import Home from "./pages/Home";
import ProductList from "./pages/ProductList";
import Product from "./pages/Product";
import Register from "./pages/Register";
import Login from "./pages/Login";
import Cart from "./pages/Cart";
import Success from "./pages/Success";
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Navigate
} from "react-router-dom";
import {useSelector} from "react-redux";

function App() {
    const user = useSelector(state => state.user.currentUser)

    let url_prefix = '/react-ecommerce-frontend'

    return (
        <Router>
            <Routes>
                <Route path={`${url_prefix}/`} element={<Home />} />
                <Route path={`${url_prefix}/products/:category`} element={<ProductList/>} />
                <Route path={`${url_prefix}/product/:id`} element={<Product/>} />
                <Route path={`${url_prefix}/cart`} element={<Cart/>} />
                <Route path={`${url_prefix}/success`} element={<Success/>} />
                <Route path={`${url_prefix}/login`} element={user ? <Navigate to="/" /> : <Login/> } />
                <Route path={`${url_prefix}/register`} element={user ? <Navigate to="/" /> : <Register/> } />
            </Routes>
        </Router>
    );
}
export default App;
