import {createSlice} from "@reduxjs/toolkit";

const attentionSlice = createSlice({
    name: "attention",
    initialState: {
        understand: false,
    },
    reducers: {
        userUnderstands: (state) => {
            state.understand = true
        }
    }
})

export const {userUnderstands} = attentionSlice.actions
export default attentionSlice.reducer;